//mobilebar

@for $index from 0 through 20 {
  .animation-fade-index-#{$index} {
    animation: animation-bottom-top 0.38s both;
    // animation-delay: 0.1s * $index;
    animation-delay: 0.05s * $index;
  }
}
@-webkit-keyframes animation-bottom-top {
  0% {
    opacity: 0;
    // transform: translateY(60px); 
    transform: scale(0.97);
}
30% {
	opacity: 0.3;
}
  100% {
    opacity: 1;
    // transform: translateY(0);
     transform: scale(1);
	} 
}

@keyframes animation-bottom-top {
  0% {
    opacity: 0;
    transform: scale(0.97);
    // transform: translateY(60px); 
}
30% {
	opacity: 0.3;
}
  100% {
    opacity: 1;
    // transform: translateY(0); 
    transform: scale(1);
} 
}

@media (max-width: 999px) {
	.popup-show {
		.header-sticky.is-scrolling-down[data-sticky="up"] {
			transition: none;
			transform: unset;
			box-shadow: none;
			.section-header, .section-announcement-bar {
				opacity: 0;
			}
		}
	}
}
.mobilebar {
	z-index: 10;
	// padding-bottom: env(safe-area-inset-bottom);
	.mobile-icon-list {
		border-top: 1px solid rgba(0,0,0,0.1);
		.bubble-count {
			@include ab(-8px,n,n,-10px);
			width: 16px;
			height: 16px;
			font-size: 7px;
			line-height: 8px;
			.n-item {
				margin-left: 1px;
			}
		}
		a {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			width: 100%;
			>div {
				display: flex;
				position: relative;
			}
		}
		.text {
			font-weight: 500;
			width: 100%;
			line-height: 1;
			padding-top: 3px;
			font-size: 12px;
		}
		.wishlist-target {
			margin-top: 1px;
		}
		.wishlist-target ~ .text {
			padding-top: 4px;
		}
	}
	.header-icon--hamburger {
		display: flex;
		align-items: center;
		justify-content: center;
		span {
			display: flex;
			justify-content: center;
		}
		svg {
			width: 20px;
			height: 20px;
		}
		.icon-on-close {
			display: none;
			svg {
				path {
					stroke: var(--primary-color);
				}
			}
		}
		.active {
			.icon-on-close {
				display: flex;	
			}
			.icon-on-show {
				display: none;
			}
		}
	}
}
.mobile-navigation {
	background: var(--header-background-color);
	@include fixed(100%,0,45px,0);
	z-index: -1;
	overflow: hidden;
	overflow-y: auto;
	// opacity: 0;
	// visibility: hidden;
	transition: 0.28s all ease-in-out;
	padding-bottom: env(safe-area-inset-bottom);
	display: none;
	.mobile-navigation-close {
		@include ab(16px,n,n,16px);
		display: flex;
		z-index: 18;
		padding: 13px;
		background: var(--miscellaneous-color);
		border-radius: var(--border-radius);
	}
	.header-multi-shop {
		padding-bottom: 22.5px;
	}
	.multi-shop-list {
		margin: 0 -5px;
		>li {
			padding: 0 5px;
		}
		details {
			padding: 11px;
			border: 1px solid var(--miscellaneous-color);
			color: var(--header-text-color);
			min-width: 85px;
			font-weight: 400;
			height: 40px;
			@extend %radius-5px;
			summary {
				>div {
					justify-content: center;
					flex-wrap: nowrap;
				}
			}
			svg {
				path {
					fill: var(--header-text-color);
				}
			}
		}
	}
}
.mobile-navigation-header {
	.layer-title {
		padding-top: 14px;
		padding-bottom: 25px;
		margin-bottom: 20px;
		border-bottom: 1px solid var(--miscellaneous-color);
		button {
			font-size: 17px;
			line-height: 1.47em;
			align-items: center;
			font-weight: 500;
			color: inherit;
		}
		svg {
			margin-top: -2px;
			width: 7px;
			height: 10px;
			@include margin-end(15px);
		}
	}
}
.mobile-navigation-layers {
	.mobile-navigation-layer {
		@include ab(0,0,0,0);
		z-index: 14;
		padding: 16px;
		// opacity: 0;
		// visibility: hidden;
		height: 100%;
		overflow: hidden;
		overflow-y: auto;
		background: var(--header-background-color);
		@extend %transition;
		display: none;
		&.active {
			// opacity: 1;
			// visibility: visible;
			display: block;
		}
	}
	>.mobile-navigation-layer:nth-child(1) {
		.animation-fade-index-0 {

		}
	}
	.sub-main-layer {
		left: 100%;
		&.active {
			left: 0;
		}
	}
	.main-layer {
		>.mobile-navigation-body {
			padding-top: 20px;
			padding-bottom: env(safe-area-inset-bottom);
		}
	}
}
.mobile-navigation-body {
	li {
		a {
			display: flex;
			align-items: center;
			padding: 7.5px 0;
			line-height: 1.475em;
			font-size: 17px;
			font-weight: 500;
		}
		>a {
			width: 100%;
		}
		details {
			width: 100%;
			&[open] {
				>summary {
					>svg {
						transform: rotate(180deg);
						transition: 0.2s all ease-out;
					}
				}
			}
		}
		summary {
			>a {
				width: calc(100% - 35px);
			}
		}
	}
	.list-item {
		display: flex;
		align-items: center;
		a {
			color: var(--header-link-color);
			font-weight: var(--header-link-fw);
			span:not(.expand) {
				flex: 1;
			}
			.expand {
				display: none;
				flex: 0 0 35px;
				@include text-right;
				margin-top: -3px;
				svg {
					transform: rotate(-90deg);
				}
			}
		}
	}
	.has-sub-layer {
		a {
			.expand {
				display: block;
				svg {
					path {
						fill: var(--header-link-color);
					}
				}
			}
		}
		.list-item {
			a {
				color: var(--header-dropdown-text-color);
				font-weight: var(--header-dropdown-text-fw);
			}
		}
	}
	.menu-title {
		font-weight: 700;
	}
	.mega-block {
		padding-bottom: 20px;
		margin-bottom: 20px;
		border-bottom: 1px solid var(--miscellaneous-color);
		&:last-child {
			margin: 0;
			border: 0;
		}
	}
	.mega-tab-list-items {
		padding-bottom: 22.5px;
		margin-bottom: 22.5px;
		border-bottom: 1px solid var(--miscellaneous-color);
	}
}
mobile-navigation {
	&.active {
		.mobile-navigation {
			z-index: 222;
			// opacity: 1;
			// visibility: visible;
			top: 0;
			display: block;
		}
	}
}
// end mobilebar 